import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import {
  AppBar, Badge, Button, Container, Divider, Hidden,
  IconButton, List, ListItem, ListItemAvatar, ListItemText, Toolbar, Typography,
} from '@material-ui/core';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import MoreIcon from '@material-ui/icons/MoreVert';
import { AiFillCaretDown, AiFillCaretUp } from 'react-icons/ai';
import Popup from 'reactjs-popup';
import { MdAddShoppingCart, MdFavoriteBorder, MdPersonOutline } from 'react-icons/md';
import _ from 'lodash';
import { HEADER_LINKS } from 'constants/index';
import cx from 'classnames';
import useStyles from './styles';

const Header = () => {
  const classes = useStyles();
  const [subItems, setSubItems] = useState(false);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = useState(null);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMobileMenuOpen = (event) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };

  const showSubItems = () => setSubItems(!subItems);

  const mobileMenuId = 'primary-search-account-menu-mobile';
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
      <List className={classes.root}>
        <ListItem>
          <ListItemAvatar>
            <IconButton
              aria-label="account of current user"
              aria-controls="primary-search-account-menu"
              aria-haspopup="true"
              color="inherit"
              className="p-1"
            >
              <MdPersonOutline />
            </IconButton>
          </ListItemAvatar>
          <Link to="/sign-in" className={classes.navLink}>
            <ListItemText primary="Profile" />
          </Link>

        </ListItem>
        <ListItem>
          <ListItemAvatar>
            <IconButton aria-label="show 17 new notifications" color="inherit" className="p-1">
              <Badge badgeContent={17} color="secondary">
                <MdFavoriteBorder />
              </Badge>
            </IconButton>
          </ListItemAvatar>
          <Link to="/" className={classes.navLink}>
            <ListItemText primary="Favoris" />
          </Link>
        </ListItem>
        <ListItem>
          <ListItemAvatar>
            <IconButton aria-label="show 11 new notifications" color="inherit" className="p-1">
              <Badge badgeContent={10} color="secondary">
                <MdAddShoppingCart />
              </Badge>

            </IconButton>
          </ListItemAvatar>
          <Link to="/" className={classes.navLink}>
            <ListItemText primary="Panier" />
          </Link>
        </ListItem>
        {_.map(HEADER_LINKS, ((item) => (
          <>
            <Divider component="li" />
            {item.links

              ? (
                <>
                  <ListItem>
                    <ListItemText primary={item.title} onClick={showSubItems} />
                    {subItems ? <AiFillCaretUp className="ml-2" /> : <AiFillCaretDown className="ml-2" /> }
                  </ListItem>
                  {subItems && (
                    _.map(item.links, (sublink) => (
                      <Link to={sublink.link} className={classes.navLink}>
                        <ListItem>
                          <ListItemText primary={sublink.title} />
                        </ListItem>
                      </Link>
                          ))
                          ) }
                </>
              )
            : (
              <Link to={item.link} className={classes.navLink}>
                <ListItem>
                  <ListItemText primary={item.title} />
                </ListItem>
              </Link>

           )}
          </>
        )
        ))}
      </List>
    </Menu>
  );
  return (
    <>

      <AppBar position="fixed" className={classes.appBar} color="inherit">
        <Container>
          <div className={classes.sectionDesktop}>
            <Toolbar className={classes.grow}>
              <Link to="/">
                <img
                  src="react-app/images/logo-jhonnyBikes1.png"
                  alt="jhonny-bikes"
                  height="75px"
                  className={cx(classes.image)}
                />
              </Link>
              <Popup
                className="popup-content"
                trigger={(
                  <Link to="/">
                    <img
                      src="react-app/images/black-jhonnybikes.png"
                      alt="jhonny-bikes"
                      height="60px"
                      className={cx(classes.image, 'mt-3')}
                    />
                  </Link>
            )}
                position="bottom center"
                on="hover"
              >
                <div style={{ background: 'white', width: '250px' }} className="p-1">
                  <MenuItem className={cx(classes.navLinkItem, ' mt-3 mb-2 ')}>
                    <Link style={{ color: 'inherit' }} to="/about">

                      à propos de nous
                    </Link>
                  </MenuItem>
                  <MenuItem className={cx(classes.navLinkItem, 'mb-2 ')}>
                    <Link style={{ color: 'inherit' }} to="/">
                      FAQ
                    </Link>

                  </MenuItem>
                </div>
              </Popup>
              <Hidden>
                <div className={classes.linksContainer}>
                  {HEADER_LINKS.map((item) => (
                    item.links

                     ? (
                       <Popup
                         className="popup-content"
                         trigger={(
                           <Button classes={{ root: classes.button }} className={classes.navLink}>
                             {item.title}
                             <AiFillCaretDown className="ml-2" />
                           </Button>
            )}
                         position="bottom center"
                         on="hover"
                       >
                         <div style={{ background: 'white' }} className="mt-3 p-1">

                           {_.map(item.links, (link) => (
                             <MenuItem className={cx(classes.navLinkItem, 'mb-2')}>
                               <Link style={{ color: 'inherit' }} to={link.link}>
                                 {link.title}
                               </Link>

                             </MenuItem>
))}

                         </div>

                       </Popup>
)

                     : (
                       <Button classes={{ root: classes.button }}>
                         <Link to={item.link} className={classes.navLink}>
                           {item.title}
                         </Link>
                       </Button>
)
              ))}
                </div>
              </Hidden>

            </Toolbar>

            <div className={classes.iconButtons}>
              <Popup
                className="popup-content"
                trigger={(
                  <IconButton aria-label="show 4 new mails" color="inherit">
                    <Badge badgeContent={10} color="secondary">
                      <MdAddShoppingCart size="35" />
                    </Badge>
                  </IconButton>
      )}
                position="bottom right"
                on="hover"
              >

                <div style={{ background: 'white' }}>
                  you're card is empty

                </div>

              </Popup>

              <Link to="/sign-in" className={classes.navLink}>
                <IconButton aria-label="show 17 new notifications" color="inherit">
                  <Badge badgeContent={17} color="secondary">
                    <MdFavoriteBorder size="35" />
                  </Badge>
                </IconButton>
              </Link>
              <Link to="/sign-in" className={classes.navLink}>
                <IconButton
                  aria-label="account of current user"
                  aria-controls="primary-search-account-menu"
                  aria-haspopup="true"
                  color="inherit"
                >

                  <MdPersonOutline size="35" />
                </IconButton>
              </Link>

            </div>

          </div>
          <div className={classes.sectionMobile}>
            <Toolbar className={classes.grow}>
              <Typography component={Link} to="/" variant="h5" className={classes.title} color="inherit">
                <img src="react-app/images/logo-jhonnyBikes1.png" alt="jhonny-bikes" height="50px" className={classes.image} />
                <img src="react-app/images/black-jhonnybikes.png" alt="jhonny-bikes" height="50px" className="mt-2" />

              </Typography>

            </Toolbar>
            <div className={classes.iconButtons}>
              <IconButton
                aria-label="show more"
                aria-controls={mobileMenuId}
                aria-haspopup="true"
                onClick={handleMobileMenuOpen}
                color="inherit"
              >
                <MoreIcon />
              </IconButton>
            </div>
            {renderMobileMenu}

          </div>

        </Container>

      </AppBar>

      <br />
      <br />
      <br />

    </>

  );
};

export default Header;
